import React from "react";
import { BellOutlined } from "@ant-design/icons";
import { Button, Checkbox, Form } from "antd";

const GOOGLE_PLAY_LOGO =
    "data:image/png;base64,";
const APP_STORE_LOGO =
    "data:image/png;base64,";

export const AppNotification: React.FC = () => {
    const [animate, setAnimate] = React.useState(false);
    const [showNotif, setShowNotif] = React.useState(false);
    const submitForm = (values: any) => {};
    let _TimeOut: any = null;

    React.useEffect(() => {
        // setTimeout(() => {
        //     setShowNotif(true);
        // }, 500);
        // setTimeout(() => {
        //     setAnimate(true);
        // }, 1000);
    }, []);

    return (
        <>
            <div className={`ss-dashboard-app-notification${showNotif ? " show animated animated animate__bounceIn" : ""}`}>
                <div className="ss-dashboard-app-notification-wrap">
                    <div className="ss-dashboard-app-notification-icon">
                        <BellOutlined
                            className={`bell-icon${animate ? " animated animate__tada" : ""}`}
                            onClick={() => {
                                if (_TimeOut) _TimeOut = null;
                                setAnimate(true);
                                _TimeOut = setTimeout(() => {
                                    setAnimate(false);
                                }, 1500);
                            }}
                        />
                    </div>
                    <div className="ss-dashboard-app-notification-body">
                        <div className="app-notification-body-title">
                            <span>Self Service is now available in Mobile App.</span>
                        </div>
                        <div className="app-notification-body-message">
                            <p>
                                <a
                                    rel="noreferrer"
                                    target="_blank"
                                    href="https://play.google.com/store/apps/details?id=com.affinityteam.affinitymobile">
                                    <img src={GOOGLE_PLAY_LOGO} alt="Google Play Store" />
                                </a>
                                <a rel="noreferrer" target="_blank" href="https://apps.apple.com/us/app/affinity-self-service/id6449438365">
                                    <img src={APP_STORE_LOGO} alt="App Store" />
                                </a>
                            </p>
                        </div>
                        <div className="app-notification-body-footer">
                            <Form name="Notif" onFinish={submitForm}>
                                <Form.Item name="dont_show" noStyle>
                                    <Checkbox style={{ marginRight: "4px" }} />
                                </Form.Item>
                                <span style={{ fontSize: "12px" }}>Don't show this again</span>
                                <Form.Item noStyle>
                                    <Button type="primary" size="small" htmlType="submit">
                                        OK
                                    </Button>
                                </Form.Item>
                            </Form>
                        </div>
                    </div>
                </div>
            </div>
            <div className={`ss-dashboard-app-notification-overlay${showNotif ? " show" : ""}`}></div>
        </>
    );
};
